@import './_colors.scss';

button {
  cursor: pointer;
  border: none;
  padding: 1rem 2rem;
  outline: none;
  background: $red;
  color: white;
  border-radius: .3rem;
  font-family: inherit;
  text-transform: uppercase;
  font-size: 25px;
  &:hover {
    background: $redHover;
  }
}

input {
  padding: 1rem;
  width: 100%;
  border: 0;
  border-bottom: solid 2px $red;
  outline: none;
  font-family: inherit;
  font-size: 2rem;
  color: white;
  background: transparent;

  &::placeholder {
    color: white;
  }
}